* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.page {
    min-height: 100vh;
    .conent-area {
        width: calc(100% - 250px);
        min-height: 100%;
        transition: 0.3s;
        float: right;
        will-change: width;
        &.no-sidebar {
            width: 100%;
        }
    }
}